<template>
    <div class="max-w-4xl mx-auto p-4">
      <div v-if="currentScene === 'intro'" class="text-center space-y-6">
        <h1 class="text-3xl font-bold">圖書館密室逃脫</h1>
        <p class="text-lg">你被鎖在一間神秘的圖書館裡，需要找出線索才能逃出去...</p>
        <button
          @click="currentScene = 'bookshelf'"
          class="bg-blue-500 text-white px-6 py-3 rounded-lg hover:bg-blue-600"
        >
          開始遊戲
        </button>
      </div>
  
      <div v-else-if="currentScene === 'bookshelf'" class="space-y-6">
        <div class="bg-amber-100 p-6 rounded-lg shadow-lg">
          <div class="flex justify-between items-center mb-4">
            <h2 class="text-xl font-bold">圖書館書架</h2>
            <div class="text-sm text-gray-600">
              已找到的線索: {{ foundClues.length }} / {{ books.filter(b => b.clue).length }}
            </div>
          </div>
          <div class="grid grid-cols-4 gap-4 mb-6">
            <BookComponent
              v-for="book in books"
              :key="book.id"
              :book="book"
              :onClick="handleBookClick"
              :isFound="foundClues.includes(book.id)"
            />
          </div>
          <button v-if="gameProgress.hasKey"
            @click="currentScene = 'lock'"
            class="mt-4 bg-green-500 text-white px-4 py-2 rounded hover:bg-green-600"
          >
            前往密碼鎖
          </button>
        </div>
      </div>
  
      <div v-else-if="currentScene === 'lock'" class="space-y-6">
        <button
          @click="currentScene = 'bookshelf'"
          class="flex items-center text-gray-600 hover:text-gray-900"
        >
          <span class="mr-2">[ArrowLeft]</span> 返回書架
        </button>
        <div class="bg-white p-6 rounded-lg shadow-lg">
          <h2 class="text-xl font-bold mb-4">密碼鎖</h2>
          <PasswordInput onSubmit={handlePasswordSubmit} />
        </div>
      </div>
  
      <div v-else-if="currentScene === 'ending'" class="text-center space-y-6">
        <h1 class="text-3xl font-bold">恭喜你成功逃出！</h1>
        <p class="text-lg">你成功解開了所有謎題，離開了這個神秘的圖書館。</p>
        <button
          @click="resetGame"
          class="bg-blue-500 text-white px-6 py-3 rounded-lg hover:bg-blue-600"
        >
          再玩一次
        </button>
      </div>
  
      <div v-if="currentScene !== 'intro' && currentScene !== 'ending'" class="fixed bottom-4 left-4 bg-white p-2 rounded-lg shadow-lg">
        <div class="flex space-x-2">
          <div v-for="(item, index) in inventory" :key="index" class="bg-gray-100 p-2 rounded">
            <span v-if="item === 'key'">[Key]</span>
          </div>
        </div>
      </div>
  
      <div v-if="selectedBook" class="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4">
        <div class="bg-white rounded-lg p-6 max-w-md w-full">
          <h3 class="text-xl font-bold mb-4">{{ selectedBook.title }}</h3>
          <div class="mb-6">
            <p v-if="selectedBook.clue" class="text-gray-700">{{ selectedBook.clue }}</p>
            <p v-else class="text-gray-500">這本書似乎沒有特別的線索...</p>
          </div>
          <button
            @click="selectedBook = null"
            class="w-full bg-blue-500 text-white py-2 px-4 rounded hover:bg-blue-600"
          >
            關閉
          </button>
        </div>
      </div>
    </div>
  </template>
  
  <script setup>
  import { ref, reactive } from 'vue';
  import BookComponent from './BookComponent.vue'; // 假設您已經建立 BookComponent.vue
  import PasswordInput from './PasswordInput.vue'; // 假設您已經建立 PasswordInput.vue
  
  // 遊戲狀態管理
  const inventory = ref([]);
  const gameProgress = reactive({
    bookshelfSolved: false,
    hasKey: false,
    hasNote: false,
    gameCompleted: false
  });
  
  const addToInventory = (item) => {
    if (!inventory.value.includes(item)) {
      inventory.value.push(item);
    }
  };
  
  const updateProgress = (key, value) => {
    gameProgress[key] = value;
  };
  
  // 場景狀態
  const currentScene = ref('intro');
  const selectedBook = ref(null);
  const foundClues = ref([]);
  
  // 書本資料
  const books = ref([
    {
      id: 1,
      title: '古老的童話書',
      color: 'bg-red-200',
      clue: '童話書上寫著: "數字的開始是 3"',
      isKey: false
    },
    {
      id: 2,
      title: '破舊的字典',
      color: 'bg-blue-200',
      clue: '字典裡夾著一張紙條: "_ _ 4 _"',
      isKey: false
    },
    {
      id: 3,
      title: '歷史課本',
      color: 'bg-green-200',
      clue: null,
      isKey: true
    },
    {
      id: 4,
      title: '神秘的日記',
      color: 'bg-yellow-200',
      clue: '日記最後一頁寫著: "第二個數字是 5"',
      isKey: false
    },
    {
      id: 5,
      title: '數學練習簿',
      color: 'bg-purple-200',
      clue: null,
      isKey: false
    },
    {
      id: 6,
      title: '食譜',
      color: 'bg-pink-200',
      clue: '食譜角落有筆記: "最後是 7"',
      isKey: false
    },
    {
      id: 7,
      title: '地圖冊',
      color: 'bg-orange-200',
      clue: null,
      isKey: false
    },
    {
      id: 8,
      title: '詩集',
      color: 'bg-teal-200',
      clue: null,
      isKey: false
    }
  ]);
  
  // 處理書本點擊
  const handleBookClick = (book) => {
    selectedBook.value = book;
    if (book.clue && !foundClues.value.includes(book.id)) {
      foundClues.value.push(book.id);
    }
    if (book.isKey && !gameProgress.hasKey) {
      updateProgress('hasKey', true);
      addToInventory('key');
      alert('你在書中找到了一把鑰匙！');
    }
  };
  
  // 處理密碼驗證
  const handlePasswordSubmit = (password) => {
    if (password === '3547') {
      updateProgress('gameCompleted', true);
      currentScene.value = 'ending';
    } else {
      alert('密碼錯誤，請再試試看！');
    }
  };
  
  // 重新開始遊戲
  const resetGame = () => {
    currentScene.value = 'intro';
    foundClues.value = [];
    updateProgress('gameCompleted', false);
    updateProgress('hasKey', false);
    inventory.value = []; // 清空物品欄
  };
  </script>
  
  <style scoped>
  /* 組件樣式 (如果需要) */
  </style>