<template>
    <form @submit.prevent="handleSubmit" class="space-y-4">
      <input
        type="password"
        maxlength="4"
        v-model="password"
        class="w-full p-2 border rounded text-center text-2xl tracking-wider"
        placeholder="輸入四位數密碼"
      />
      <button
        type="submit"
        class="w-full bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 rounded"
      >
        確認密碼
      </button>
    </form>
  </template>
  
  <script setup>
  import { ref, defineEmits } from 'vue';
  
  const password = ref('');
  const emit = defineEmits(['submit']);
  
  const handleSubmit = () => {
    emit('submit', password.value);
    password.value = '';
  };
  </script>
  
  <style scoped>
  /* PasswordInput 樣式 (如果需要) */
  </style>